@charset "UTF-8";
/* ==================================== */
/* ======== NORMALIZE AND RESET ======= */
/* ==================================== */
/* reset */
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, html {
  height: 100%;
  width: 100%;
}

form {
  margin: 0;
  padding: 0;
}

input, input:focus, textarea, select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important;
}

button,
textarea,
.select select,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
  appearance: none;
  border-radius: 0;
}

select[disabled] {
  cursor: default;
}

select::-ms-expand {
  display: none;
}

input {
  vertical-align: middle;
}

input::-ms-clear {
  display: none;
}

input[type="radio"]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0 !important;
}

input[type="checkbox"]:focus {
  -moz-outline-offset: -1px !important;
  -moz-outline: 1px solid #000 !important;
}

input[type="radio"],
input[type="checkbox"],
select {
  cursor: pointer;
}

/* ==================================== */
/* ============== LAYOUT ============== */
/* ==================================== */
body {
  padding: 0;
  margin: 0;
}

.main {
  padding: 30px 0 0;
  position: relative;
  clear: both;
  z-index: 4;
}

.user-content {
  padding: 60px 0;
}

.content {
  margin: 0 0 2.4rem 0;
}

.header {
  position: relative;
  z-index: 5;
  background-color: #ee6e73;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.footer {
  margin: 0 auto;
  position: relative;
  clear: both;
  z-index: 5;
  background-color: lightgreen;
}

.max-wrap {
  max-width: 1960px;
}

.layout {
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 991px) {
  .layout {
    padding-right: 0;
    padding-left: 0;
  }
}

.max-wrap-text {
  max-width: 720px;
}

.layout-text {
  max-width: 720px;
  padding-right: 40px;
  padding-left: 40px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 991px) {
  .layout-text {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  .layout-text {
    padding-right: 15px;
    padding-left: 15px;
  }
}

.max-wrap-flood {
  max-width: 1040px;
}

.layout-flood {
  max-width: 1040px;
  padding-right: 40px;
  padding-left: 40px;
  margin-right: auto;
  margin-left: auto;
}

@media (max-width: 991px) {
  .layout-flood {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  .layout-flood {
    padding-right: 15px;
    padding-left: 15px;
  }
}

/*LAYOUT end*/
/* ==================================== */
/* ============ TYPOGRAPHY ============ */
/* ==================================== */
/*base */
html {
  font-size: 62.5%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 1.6rem;
  line-height: 2.4rem;
  -webkit-font-smoothing: antialiased;
  height: 100%;
  color: #262626;
  background: #fff url(../../img/empty.png) repeat fixed;
  min-width: 320px;
}

@media (max-width: 639px) {
  body {
    font-size: 1.6rem;
    line-height: 1.7;
  }
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  font-weight: 400;
  color: #ee6e73;
  margin: 0 0 2.4rem;
}

@media (max-width: 639px) {
  h1, h2, h3, h4, h5, h6 {
    margin: 0 0 2rem;
  }
}

h1 {
  font-weight: 300;
  font-size: 4.2rem;
  line-height: 5.2rem;
}

@media (max-width: 991px) {
  h1 {
    font-size: 3.4rem;
    line-height: 4.2rem;
  }
}

@media (max-width: 639px) {
  h1 {
    font-size: 3rem;
    line-height: 3.7rem;
  }
}

h2 {
  font-size: 3rem;
  line-height: 4.2rem;
}

@media (max-width: 991px) {
  h2 {
    font-size: 2.8rem;
    line-height: 3.9rem;
  }
}

@media (max-width: 639px) {
  h2 {
    font-size: 2.4rem;
    line-height: 3.4rem;
    letter-spacing: 0.01em;
  }
}

h2::before {
  content: "§ ";
  display: inline;
}

h3 {
  font-size: 2.4rem;
  line-height: 3rem;
}

@media (max-width: 991px) {
  h3 {
    font-size: 2.2rem;
    line-height: 2.8rem;
  }
}

@media (max-width: 639px) {
  h3 {
    font-size: 1.8rem;
    line-height: 2.3rem;
    letter-spacing: 0.02em;
  }
}

h4 {
  font-size: 1.6rem;
  line-height: 2.4rem;
}

h5 {
  font-size: 1.6rem;
  line-height: 2.4rem;
}

h6 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 1.6rem;
  line-height: 2.4rem;
  text-transform: uppercase;
}

.text-title {
  font-weight: bold;
  font-size: 1.8rem;
  line-height: 2.4rem;
  margin-bottom: 1.2rem;
}

.content .text-title + ul,
.content .text-title + ol,
.content .text-title + table {
  margin-top: 1.2rem;
}

p {
  padding: 0;
  margin: 0 0 2.4rem;
}

hr {
  line-height: 1px;
  height: 0;
  border: none;
  background: none;
  border-bottom: 1px solid #aaa;
  margin-bottom: 2.4rem;
}

a {
  color: blue;
  text-decoration: underline;
  cursor: default;
}

a:link {
  cursor: pointer;
}

.no-touchevents a:hover {
  text-decoration: none;
}

a[href^="tel"],
.no-touchevents a[href^="tel"]:hover {
  text-decoration: none !important;
  color: inherit;
  cursor: default;
}

a[href^="javascript:"] {
  cursor: default;
}

.bd-lead {
  font-size: 2.3rem;
  line-height: 3.5rem;
  font-weight: 300;
}

img {
  border: none;
  vertical-align: top;
  max-width: 100%;
}

table img {
  max-width: none;
}

.table-auto {
  position: relative;
  overflow: auto;
  max-width: 100%;
  -webkit-overflow-scrolling: touch;
  margin: 0 0 2.4rem;
}

.table-auto table {
  margin-bottom: 0;
  width: auto;
  min-width: 100%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

table td, table th {
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: #262626;
  text-align: left;
  padding: 1rem 1.2rem;
  box-sizing: border-box;
  border-color: #aaa;
}

b, strong, th {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
  font-weight: bold;
  font-style: italic;
}

.mark {
  color: #ff0b4f;
  font-style: inherit;
  font-weight: inherit;
}

.text-color-alt {
  color: #aaa;
}

.align-center {
  text-align: center;
}

.nobr {
  white-space: nowrap;
}

.bottom-space {
  margin-bottom: 2.4rem;
}

.hide {
  font-size: 0;
  line-height: 0;
}

@media (max-width: 1919px) {
  .hide-desk-s {
    display: none !important;
  }
}

@media (max-width: 1599px) {
  .hide-lap {
    display: none !important;
  }
}

@media (max-width: 1365px) {
  .hide-net {
    display: none !important;
  }
}

code.l, code.red, code.green {
  display: inline;
  padding: 0 2px;
  margin: 0;
}

.green {
  color: lightseagreen;
}

.red {
  color: lightcoral;
}

.gray {
  color: #999999;
}

.warning {
  display: block;
  background-image: url(../../img/icon-warning.svg);
  background-repeat: no-repeat;
  background-position: 10px 10px;
  background-size: 24px;
  padding: 10px 10px 10px 45px;
  background-color: rgba(238, 110, 115, 0.1);
}

.accent {
  font-size: 1.2em;
}

.article-content p + h2,
.article-content ul + h2,
.article-content ol + h2 {
  margin-top: 2.4rem;
}

@media (max-width: 639px) {
  .article-content p + h2,
  .article-content ul + h2,
  .article-content ol + h2 {
    margin-top: 2rem;
  }
}

.article-content p + h3,
.article-content ul + h3,
.article-content ol + h3 {
  margin-top: 2.4rem;
}

@media (max-width: 639px) {
  .article-content p + h3,
  .article-content ul + h3,
  .article-content ol + h3 {
    margin-top: 2rem;
  }
}

.article-content p + h4,
.article-content ul + h4,
.article-content ol + h4 {
  margin-top: 2.4rem;
}

@media (max-width: 639px) {
  .article-content p + h4,
  .article-content ul + h4,
  .article-content ol + h4 {
    margin-top: 2rem;
  }
}

.article-content p + h5,
.article-content ul + h5,
.article-content ol + h5 {
  margin-top: 2.4rem;
}

@media (max-width: 639px) {
  .article-content p + h5,
  .article-content ul + h5,
  .article-content ol + h5 {
    margin-top: 2rem;
  }
}

/*base end */
/*table */
.article-content table {
  margin-bottom: 2.4rem;
}

.article-content .table-auto table {
  margin-bottom: 0;
}

.article-content .tbl {
  border: none;
}

.article-content .tbl td,
.article-content .tbl th {
  color: #262626;
  background-color: #f2f2f2;
  border: 0 solid #aaa;
  font-size: 1.4rem;
  line-height: 1.2;
}

.article-content .tbl td:first-child, .article-content .tbl td:first-child,
.article-content .tbl th:first-child,
.article-content .tbl th:first-child {
  border-left-width: 1px;
}

.article-content .tbl td:last-child, .article-content .tbl td:last-child,
.article-content .tbl th:last-child,
.article-content .tbl th:last-child {
  border-right-width: 1px;
}

.article-content .tbl td {
  border-top-width: 1px;
}

.article-content .tbl tbody tr:first-child td {
  border-top-width: 0;
}

.article-content .tbl tbody tr:nth-child(even) td {
  background: #fafafa;
}

.article-content .tbl th,
.article-content .tbl thead td {
  font-weight: bold;
  font-size: 1.6rem;
  text-align: left;
}

.article-content .tbl th,
.article-content .tbl thead td,
.article-content .tbl tfoot td {
  background: #f2f2f2;
  color: #aaa;
  border-color: #f2f2f2;
  border-top-width: 0;
}

/*table end */
/*blockquote */
.article-content .blockquote {
  margin: 2.4rem 0;
}

.article-content blockquote {
  display: block;
  margin: 0;
  padding: 0;
}

.article-content blockquote q {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  font-size: 2rem;
  line-height: 1.5;
  color: #262626;
  z-index: 5;
}

@media (max-width: 639px) {
  .article-content blockquote q {
    font-size: 1.6rem;
    line-height: 1.7;
  }
}

.article-content blockquote q:before, .article-content blockquote q:after {
  content: "";
  display: block;
  position: relative;
  background-size: contain;
  width: 40px;
  height: 35px;
  z-index: -1;
}

.article-content blockquote q:before {
  content: "";
  margin: 0 0 20px 30%;
}

@media (max-width: 639px) {
  .article-content blockquote q:before {
    margin-bottom: 10px;
    margin-left: -10px;
  }
}

.article-content blockquote q:after {
  content: "";
  margin: 20px 30% 0 auto;
  transform: rotate(180deg);
}

@media (max-width: 639px) {
  .article-content blockquote q:after {
    content: none;
    display: none;
  }
}

.article-content blockquote cite {
  text-align: right;
  display: block;
  margin-left: auto;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #262626;
}

/*blockquote end */
/*content img */
.imgc {
  text-align: center;
  padding-bottom: 7.2rem;
  clear: both;
}

.imgl {
  text-align: left;
  float: left;
  margin: 0.72rem 7.2rem 7.2rem 0;
  max-width: 50%;
}

.imgr {
  text-align: right;
  float: right;
  margin: 0.72rem 0 7.2rem 7.2rem;
  max-width: 50%;
}

@media (max-width: 991px) {
  .imgl, .imgr {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: none;
  }
}

/*content img end */
/*view print */
.view-print {
  display: inline-block;
  vertical-align: top;
  padding: 4px 18px;
  background: white;
  font-size: 16px;
  line-height: 30px;
  color: #aaa;
  border: 1px solid #aaa;
  text-decoration: none;
  margin-bottom: 2.4rem;
  transition-property: background-color, color;
  transition-duration: 0.2s;
}

.no-touchevents .view-print:hover {
  background-color: #aaa;
  color: white;
}

.view-print:active {
  transform: scale(0.98);
}

.view-print svg {
  width: 21px;
  height: 21px;
  vertical-align: middle;
  margin-right: 10px;
}

.view-print svg path {
  fill: #aaa;
  transition-property: fill;
  transition-duration: 0.2s;
}

.no-touchevents .view-print:hover path {
  fill: white;
}

.view-print span {
  display: inline-block;
  vertical-align: middle;
}

@media (max-width: 991px) {
  .view-print {
    font-size: 14px;
  }
}

@media (max-width: 639px) {
  .view-print svg {
    margin-right: 0;
  }
  .view-print span {
    display: none;
  }
}

/*view print end */
/*video */
.video-holder {
  position: relative;
  padding-bottom: 62%;
  padding-top: 25px;
  height: 0;
}

.video-holder iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*video end */
input[type="radio"], input[type="checkbox"] {
  width: 15px;
  height: 15px;
}

/* snippet */
.btn {
  position: relative;
  display: inline-block;
  padding: 6px 12px;
  font-size: 13px;
  font-weight: 700;
  line-height: 20px;
  color: #333333;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  background-color: #eeeeee;
  background-image: linear-gradient(#fcfcfc, #eeeeee);
  border: 1px solid #d5d5d5;
  border-radius: 3px;
  user-select: none;
  -webkit-appearance: none;
}

.btn i {
  font-style: normal;
  font-weight: 500;
  opacity: 0.6;
}

.btn .octicon {
  vertical-align: text-top;
}

.btn .counter {
  text-shadow: none;
  background-color: #e5e5e5;
}

.btn:focus {
  text-decoration: none;
  border-color: #51a7e8;
  outline: 0;
  box-shadow: 0 0 5px rgba(81, 167, 232, 0.5);
}

.btn:focus:hover, .btn.selected:focus {
  border-color: #51a7e8;
}

.btn:hover, .btn:active, .btn.zeroclipboard-is-hover, .btn.zeroclipboard-is-active {
  text-decoration: none;
  background-color: #dddddd;
  background-image: linear-gradient(#eeeeee, #dddddd);
  border-color: #cccccc;
}

.btn:active, .btn.selected, .btn.zeroclipboard-is-active {
  background-color: gainsboro;
  background-image: none;
  border-color: #b5b5b5;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15);
}

.clippy {
  margin-top: -3px;
  position: relative;
  top: 3px;
  vertical-align: baseline;
}

.btn[disabled] .clippy {
  opacity: 0.3;
}

.snippet {
  position: relative;
  overflow: visible;
}

.snippet code {
  font-size: 14px;
  line-height: 20px;
}

.snippet .btn {
  transition: opacity 0.3s ease-in-out;
  opacity: 0;
  padding: 2px 6px;
  position: absolute;
  right: 4px;
  top: 4px;
}

.snippet:hover .btn, .snippet .btn:focus {
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .snippet .btn {
    opacity: 1;
  }
}

.example + .snippet .btn {
  top: 22px;
  right: 2px;
}

/* snippet end */
/* hljs */
.hljs {
  background-color: #f0f0f0;
}

/* hljs end */
/* separator */
.separator {
  background: url(../../img/icon-separator.svg) no-repeat center;
}

/* separator end */
/* article elements */
ul, ol {
  margin: 0 0 2.4rem;
}

.article-content ul, .article-content ol {
  margin: 0 0 2.4rem;
  padding: 0;
  list-style: none;
  font-size: 1.6rem;
  line-height: 2.4rem;
}

.article-content ul li, .article-content ol li {
  padding-left: 1.5rem;
  margin: 0 0 1.2rem;
  position: relative;
}

.article-content ul > li::before {
  content: "—";
  display: block;
  float: left;
  margin: 0 0 0 -1.5rem;
  color: #ee6e73;
}

.article-content ol {
  counter-reset: section;
}

.article-content ol > li {
  counter-increment: section;
  padding-left: 1.5rem;
}

.article-content ol > li::before {
  display: block;
  float: left;
  content: counter(section) ".";
  margin: 0 0 0 -1.5rem;
  color: #ee6e73;
}

.article-content ul ul, .article-content ul ol, .article-content ol ul, .article-content ol ol {
  margin: 1.2rem 0;
}

